.Toolbar {   
    background-color: rgba(0,0,0,.5);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    height: 100%;
    max-height: 100%;
    
   
    z-index: 90;
}

.Toolbar nav {
    height: 100%;
}



@media (max-width: 499px) {
    .DesktopOnly {
        display: none;
    }
}


