.Coverflow {
  -webkit-perspective: 25vw;
          perspective: 25vw;
  font-size: 16vh;
  overflow: hidden;
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  scroll-behavior: smooth;

}

.Ul {
  display: -ms-flexbox;
  display: flex;
  position: relative;
  list-style-type: none;
  padding: 0;
  scroll-behavior: smooth;

}

.WheelAnimation {
  -o-transition: all 0.1s;
  transition: all 0.1s;
  -webkit-transition: all 0.1s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.DbClickAnimation {
  -o-transition: all ease 0.6s;
  transition: all ease 0.6s;
  -webkit-transition: all ease 0.6s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.Left {
  /*  transform: rotateY(20deg) !important; */
}

.Right {
  /*   transform: rotateY(-20deg) !important; */
}

.Middle {
  -webkit-transform: rotateY(0deg) !important;
          transform: rotateY(0deg) !important;
  margin: 0rem 3rem !important;

  z-index: 250 !important;
  -webkit-transform: scale(1.3) !important;
      -ms-transform: scale(1.3) !important;
          transform: scale(1.3) !important;
}

.ClickedFront {
  z-index: 250 !important;
  -webkit-animation: CLICKED_FRONT 1.2s forwards;
          animation: CLICKED_FRONT 1.2s forwards;
}

.ClickedBack {
  z-index: 250 !important;
  -webkit-animation: CLICKED_BACK 1.2s forwards;
          animation: CLICKED_BACK 1.2s forwards;
}

.Flipper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -ms-flex-pack: center;
      justify-content: center;
  opacity: 1;
  /* transform: rotateY(-20deg); */
  margin-top: 0px;
  margin: 1rem;
  height: 20vw;
  width: 20vw;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -o-transition: all ease 0.75s;
  transition: all ease 0.75s;
  -webkit-transition: all ease 0.75s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.Image {
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  max-width: 100%;

}

.Description {
  position: absolute;
  font-size: 14px;
}

.Front {
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  /* -webkit-box-reflect: below 0px -webkit-gradient(linear, left top, left bottom, from(transparent), to(rgba(250, 250, 250, 0.1))); */
}

.Front,
.Back {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
}

@-webkit-keyframes CLICKED_BACK {
  0% {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg)
  }

  70% {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg)
  }

  100% {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg)
  }
}

@keyframes CLICKED_BACK {
  0% {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg)
  }

  70% {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg)
  }

  100% {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg)
  }
}

@-webkit-keyframes CLICKED_FRONT {
  0% {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg)
  }

  70% {
    -webkit-transform: rotateY(-180deg);
            transform: rotateY(-180deg)
  }

  100% {
    -webkit-transform: rotateY(-180deg);
            transform: rotateY(-180deg)
  }
}

@keyframes CLICKED_FRONT {
  0% {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg)
  }

  70% {
    -webkit-transform: rotateY(-180deg);
            transform: rotateY(-180deg)
  }

  100% {
    -webkit-transform: rotateY(-180deg);
            transform: rotateY(-180deg)
  }
}


