.NavigationItem {
    margin: 10px 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: block;
    width: 100%;
}

.NavigationItem a {
    color: #ccc;
    text-decoration: none;
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: block;
    font-size: 15px;
}

.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active {
    color: #72D687;
  

}

@media (min-width: 500px) {
    .NavigationItem {
        margin: 1px;
        display: -ms-flexbox;
        display: flex;
        height: 100%;
        width: auto;
        -ms-flex-align: center;
            align-items: center;
    }
    
    .NavigationItem a {
        color: white;
        height: 100%;
        padding: 16px 10px;
        border-bottom: 4px solid transparent;
    }
    
    .NavigationItem a:hover,
    .NavigationItem a:active,
    .NavigationItem a.active {
        color: #ccc;
        border-bottom: 4px solid  #72D687;
        color: white;
    }
}


