.PlayButton {
  margin-right: 1rem;
  background: transparent;
  border: none;
  outline: none;
}

.isEnabled {
  cursor: pointer;
  border: 2px solid #72D687;
}

.Icon {
  display: block;
  width: 3.5rem;
  height: 3.5rem;
  fill: #ffff;
}

.IconShape {
  fill: #ffff;
}

@-webkit-keyframes play18 {
  0% {
    background-position: 0px 0px;
  }

  100% {
    background-position: -432px 0px;
  }
}

@keyframes play18 {
  0% {
    background-position: 0px 0px;
  }

  100% {
    background-position: -432px 0px;
  }
}

@-webkit-keyframes play19 {
  0% {
    background-position: -432px 0px;
  }

  100% {
    background-position: 0px 0px;
  }
}

@keyframes play19 {
  0% {
    background-position: -432px 0px;
  }

  100% {
    background-position: 0px 0px;
  }
}

.Shapeshifter {
  -webkit-animation-duration: 300ms;
          animation-duration: 300ms;
  -webkit-animation-timing-function: steps(18);
          animation-timing-function: steps(18);
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  width: 24px;
  height: 24px;
  background-repeat: no-repeat;
  color: #ffff;
  margin-right: 6px;
  margin-left: -8px;
  margin-top: -2px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -webkit-transform: scale(2);
      -ms-transform: scale(2);
          transform: scale(2);
}

.Shapeshifter:hover {
  -webkit-transform: scale(2.3);
      -ms-transform: scale(2.3);
          transform: scale(2.3);
  cursor: pointer;
  color: #282828;
}

.Shapeshifter.Play {
  -webkit-animation-name: play18;
          animation-name: play18;
}

.Shapeshifter.Pause {
  -webkit-animation-name: play19;
          animation-name: play19;
}


