.Input {
    font-family: inherit;
    font-size: 1.6rem;    
    color: #666;    
    border: none;
    padding: .7rem 2rem;
    background: #e5e5e5;    
    border-radius: 8px; 
    -webkit-transition: all .2s; 
    -o-transition: all .2s; 
    transition: all .2s;   
    width: 80%;  
    margin-right: -3.25rem;
    caret-color: #1db954;
}

.Input:focus {
    outline: none;
    width: 90%;
    background-color: #ffff; 
}

.Button {
    border: none;
}

.Button:focus {
    outline: none;
}






.Search {

    position: relative;
 
    -ms-flex: 0 0 40%;
 
        flex: 0 0 40%;
    display: -ms-flexbox;
    display: flex; 
   
    -ms-flex-align: center; 
   
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}








